.about {
  position: relative;
  z-index: 50;
  margin-top: -1px;
  background-color: $color-darkbluegrey;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;

  &::before {
    position: absolute;
    top: 0;
    left: -20px;
    content: "";
    width: 133px;
    height: 132px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    background-color: $color-darkbluegreytwo;
  }
}

.about__background {
  position: relative;
  background-image: url("../img/about-mobile@1x.png");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: 292px 352px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/about-mobile@2x.png");
  }
}

.about__wrapper {
  @include container-padding;
  @include container-size;
  padding-top: 30px;
  padding-right: 50px;
  padding-left: 50px;
  padding-bottom: 65px;
}

.about__title {
  @include reset-margin;
  margin-bottom: 18px;
  font-size: 24px;
  font-weight: bold;
  color: $color-mango;
}

.about__text {
  @include reset-margin;
  margin-bottom: 18px;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.38;
  color: #ffffff;

  &--bold {
    font-weight: bold;
  }
}

.about__text br {
  display: none;
}

.about__button {
  display: none;
}

@media (min-width: $tablet-width) {
  .about {
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;

    &::before {
      top: -9px;
      left: 40px;
      width: 89.5%;
      height: 213px;
    }
  }

  .about__background {
    background-image: url("../img/about-tablet@1x.png");
    background-position: 98% 40px;
    background-size: 376px 548px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/about-tablet@2x.png");
    }
  }

  .about__wrapper {
    padding-left: 75px;
    padding-right: 51%;
    padding-bottom: 90px;
  }

  .about__title {
    margin-bottom: 40px;
    font-size: 45px;
    line-height: 1;
  }

  .about__text {
    font-size: 28px;
    line-height: 1.18;

    &--bold {
      margin-top: 34px;
      margin-bottom: 53px;
      font-size: 21px;
    }
  }

  .about__text br {
    display: block;
  }

  .about__text--bold br {
    display: none;
  }

  .about__desc {
    width: 225px;
  }

  .about__button {
    padding-top: 0;
    padding-right: 35px;
    padding-left: 35px;
    padding-bottom: 0;

    width: 91%;
    display: flex;
    font-size: 24px;

    &::before {
      right: 35px;
    }
  }
}

@media (min-width: $desktop-width) {
  .about {
    margin-top: -61px;
    padding-top: 148px;

    &::before {
      display: none;
    }
  }

  .about__background {
    background-image: none;
  }

  .about__wrapper {
    position: relative;
    padding-top: 40px;
    padding-right: 670px;
    padding-left: 131px;
    padding-bottom: 164px;

    background-image: url("../img/about-desktop@1x.png");
    background-repeat: no-repeat;
    background-size: 630px 665px;
    background-position: 92.5% top;

    &::before {
      position: absolute;
      top: -15px;
      left: 60px;
      content: "";
      width: 213px;
      height: 213px;
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;
      border-bottom-left-radius: 20px;
      border-bottom-right-radius: 20px;
      background-color: $color-darkbluegreytwo;
    }

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/about-desktop@2x.png");
    }
  }

  .about__title,
  .about__text {
    position: relative;
  }

  .about__title {
    margin-bottom: 60px;
  }

  .about__text {
    margin-bottom: 42px;

    &--bold {
      margin-bottom: 85px;
    }
  }

  .about__text br {
    display: none;
  }

  .about__text--bold br {
    display: block;
  }

  .about__button {
    width: 274px;
  }
}
